import { Component, OnInit } from '@angular/core';
import { Router, ActivatedRoute } from '@angular/router';
import { ServiceService } from '../service.service';

@Component({
  selector: 'app-orderdetails',
  templateUrl: './orderdetails.component.html',
  styleUrls: ['./orderdetails.component.css']
})
export class OrderdetailsComponent implements OnInit {
  val: any;
  orderDetails: any;

  constructor(public route: Router, public service: ServiceService,private activatedRoute: ActivatedRoute) { 
    this.activatedRoute.queryParams.subscribe(params => {
      this.val = params['orderId'];
      });
  }

  
  ngOnInit() {
    this.getOrderDetails(this.val);
  }
  getOrderDetails(val): any {
    this.service.getApi('vendor/orderDetail?lang=en&orderId='+val, 0).subscribe(Response => {
      if (Response['statusCode'] == 200) {
        this.orderDetails= Response['result'];
        console.log(this.orderDetails)
      }
      else {
        console.log("error")
      }
    }, error => {
      console.log("error occur", error)
    })
  }

}



