import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { ServiceService } from '../service.service';
import { FormBuilder } from '@angular/forms';
import { ConditionalExpr } from '@angular/compiler';
import { VALID } from '@angular/forms/src/model';
declare var $ :any;

@Component({
  selector: 'app-subcatgories',
  templateUrl: './subcatgories.component.html',
  styleUrls: ['./subcatgories.component.css']
})
export class SubcatgoriesComponent implements OnInit {
  responseData: any;
  image: string | ArrayBuffer;
  url: string | ArrayBuffer;
  submitted: boolean;
  subcategoryList: any;
  addSubCategoryForm: any;
  categoryList: any;
  subCategoriesData: any;
  show: boolean=false;

  constructor(public route: Router, public service: ServiceService,public formBuilder:FormBuilder) {
    this.addSubCategoryForm=formBuilder.group({
      subCategoryName:[''],
      categoryId:[''],
      file:[''],
      subCategoryId:[''],
      categoryName:['']
    })
   }

  ngOnInit() {
    this.route.navigate(['/subcatgories'])
    this.getSubCategoryList()
    this.getCategoryList()
  }

  getCategoryList(): any {
    this.service.getApi('vendor/getCategoryList', 0).subscribe(Response => {
      if (Response['statusCode'] == 200) {
         this.categoryList= Response['result'];
      }
      else {
        console.log("error")
      }
    }, error => {
      console.log("error occur", error)
    })
  }

  getSubCategoryList(): any {
    this.service.getApi('vendor/getSubCategoryList?lang=en', 0).subscribe(Response => {
      if (Response['statusCode'] == 200) {
          this.subcategoryList= Response['result'];
          console.log(this.subcategoryList)
      }
      else {
         }
    }, error => {
      console.log("error")
       })
  }


  changeListener($event) : void {
    this.readThis($event.target);
    this.readUrl($event.target);
  }
  
  readThis(inputValue: any): void {
    var file:File = inputValue.files[0];
    var myReader:FileReader = new FileReader();
  
    myReader.onloadend = (e) => {
      this.image = myReader.result;
      }
    myReader.readAsDataURL(file);
  }

  readUrl(inputValue:any) {
    if (inputValue.files && inputValue.files[0]) {
      var reader:FileReader = new FileReader();
  
      reader.onload = (event: ProgressEvent) => {
        this.url = reader.result;
      }
      reader.readAsDataURL(inputValue.files[0]);
    }
  }

  addSubCategory(val){
    let sentData = {
    _id:val.categoryId,
    subCategoryName: val.subCategoryName,
    image: this.image,
    icons: this.image,
    status:'ACTIVE'
   }
  this.service.postApi('vendor/addSubCategory', sentData, 0).subscribe(response => {

    this.responseData = response;
    if (this.responseData['statusCode'] == 200) {
          this.route.navigate(['/subcategories']);
          this.ngOnInit();
    }
   }, error => {
   })
}

deleteSubCategory(val){
  let sentData = {
    subCategoryId: val,
    status: 'inactive'
   }
   console.log(sentData)
   this.service.postApi('admin/deleteSubCategory',sentData, 0).subscribe(response => {
    this.responseData = response;
    if (this.responseData['statusCode'] == 200) {
          this.route.navigate(['/subcategories']);
          this.ngOnInit();
       }
   }, error => {
    console.log('error occur', error)
    // this.service.showError('Server Error')
})
}

clearAll()
{
if(this.subCategoriesData) this.subCategoriesData = {}
 this.url=''
 this.show =false
}

getSubCategory(val): any {
  this.service.getApi('admin/getSubCategoryById?subCategoryId='+val, 0).subscribe(Response => {
    if (Response['statusCode'] == 200) {
      console.log(this.subCategoriesData)
        this.subCategoriesData = Response['result'];
        this.url = this.subCategoriesData.icons
        this.show=true;
        console.log(this.subCategoriesData)
    }
    else {
      console.log("error")
    }
  }, error => {
    console.log("error occur", error)
  })
}


editSubCategory(val){
  var get = $('#subcategory_Id').val();
 // var getcategory = $('#category_Id').val();
    let sentData = {
    subCategoryId: get,
    categoryId: val.categoryId,
    subCategoryName: val.subCategoryName
   }
   console.log(sentData)
    this.service.postApi('admin/editSubCategory',sentData, 0).subscribe(response => {
    this.responseData = response;
    if (this.responseData['statusCode'] == 200) {
          this.route.navigate(['/subcategories']);
          this.ngOnInit();
       }
   }, error => {
    console.log('error occur', error)
    // this.service.showError('Server Error')
})
}

onSubmit() {
  this.submitted = true;
  //console.log(this.hero);
}


}
