import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { ServiceService } from '../service.service';

@Component({
  selector: 'app-users',
  templateUrl: './users.component.html',
  styleUrls: ['./users.component.css']
})
export class UsersComponent implements OnInit {
  vendorList: any;
  responseData: any;
  isvalid: boolean;
  constructor(public route: Router, public service: ServiceService) { 
    this.isvalid=true;
  }

  ngOnInit() {
    this.route.navigate(['/users'])
    this.getVendorList()
  }
  getVendorList() {
    let sentData ={
      userType:'user'
    }
    this.service.postApi('admin/getUserList',sentData, 0).subscribe(Response => {
      if (Response['statusCode'] == 200) {
        console.log(Response['result'])
 this.vendorList= Response['result'];
      }
      else {
        console.log("error")
      }
    }, error => {
      console.log("error occur", error)
    })
  }

  deleteVendor(val,status){
    let sentData = {
      _id: val,
      status: status,
      lang: "en"
     }
     this.service.postApi('admin/deleteVendor', sentData, 0).subscribe(response => {
      this.responseData = response;
      if (this.responseData['statusCode'] == 200) {
            this.route.navigate(['/vendorlist']);
            this.ngOnInit();
         }
     }, error => {
      console.log('error occur', error)
      // this.service.showError('Server Error')
  })
  }

}
