import { Component, OnInit } from '@angular/core';
import { Router, ActivatedRoute } from '@angular/router';
import { ServiceService } from '../service.service';

@Component({
  selector: 'app-userdetails',
  templateUrl: './userdetails.component.html',
  styleUrls: ['./userdetails.component.css']
})
export class UserdetailsComponent implements OnInit {
  responseData: any;
  vendorDetails: any;
  val: any;

  constructor(public route: Router, public service: ServiceService,private activatedRoute: ActivatedRoute) { 
    this.activatedRoute.queryParams.subscribe(params => {
      this.val = params['userId'];
      });
  }

  ngOnInit() {
    this.getUserInfo(this.val);
  }
  getUserInfo(val){
    let sentData = {
      userId: val,
      lang: "en"
     }
     this.service.postApi('admin/getUserinfo', sentData, 0).subscribe(response => {
      this.responseData = response;
      
      if (this.responseData['statusCode'] == 200) {
        this.vendorDetails= response['result'];
        console.log(this.vendorDetails);
          //  this.route.navigate(['/userDetails']);
            //this.ngOnInit();
         }
     }, error => {
      console.log('error occur', error)
      // this.service.showError('Server Error')
  })
  }


}
