import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { ServiceService } from '../service.service';
import { FormControl, FormGroup, Validators,ReactiveFormsModule, FormBuilder } from '@angular/forms';

@Component({
  selector: 'app-login',
  templateUrl: './login.component.html',
  styleUrls: ['./login.component.css']
})
export class LoginComponent implements OnInit {
  loginForm: any;
  sentData: { email: any; password: any;  userType:any;};
  responseData: any;
  submitted: boolean;

  constructor(public route: Router, public service: ServiceService,public formBuilder:FormBuilder) { 
    this.loginForm=formBuilder.group({
      username:[''],
      password:['']
    })
  }

  ngOnInit() {
   // this.route.navigate(['/dashboard'])
     }

     login(val){

      console.log(val)
      this.sentData = {
        email: val.username,
        password: val.password,
        userType : "admin",
     }
     this.service.postApi('user/login', this.sentData, 0).subscribe(response => {

      this.responseData = response;
      console.log("WWWWWWWW", this.responseData)
      if (this.responseData['statusCode'] == 200) {
            this.route.navigate(['/dashboard']);
            this.ngOnInit();
            localStorage.setItem('accessToken',this.responseData['accessToken'])
            localStorage.setItem('username',this.responseData['result']['firstName'] + " " + this.responseData['result']['lastName'] )
            localStorage.setItem('userimage',this.responseData['result']['image'])
      }
     }, error => {
     // console.log('error occur', error)
     
  })
    }
    onSubmit() {
      this.submitted = true;
      //console.log(this.hero);
    }

}
