import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { ServiceService } from '../service.service';
import { FormControl, FormGroup, Validators,ReactiveFormsModule, FormBuilder } from '@angular/forms';


@Component({
  selector: 'app-forgotpassword',
  templateUrl: './forgotpassword.component.html',
  styleUrls: ['./forgotpassword.component.css']
})
export class ForgotpasswordComponent implements OnInit {
  forgotForm: FormGroup;
  sentData: { email: any; };
  responseData: any;
  submitted: boolean;

  constructor(public route: Router, public service: ServiceService,public formBuilder:FormBuilder) { 
    this.forgotForm=formBuilder.group({
      email:['']
    })
  }

  ngOnInit() {
  }
  forgot(val){
    this.sentData = {
      email: val.email
    }

    this.service.postApi('user/forgotPassword', this.sentData, 0).subscribe(response => {

      this.responseData = response;
      //console.log("WWWWWWWW", this.responseData)
      if (this.responseData['statusCode'] == 200) {
            this.route.navigate(['/login']);
            this.ngOnInit();
         }
     }, error => {
     // console.log('error occur', error)
     
  })

  }
  onSubmit() {
    this.submitted = true;
    //console.log(this.hero);
  }
}
