import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { ServiceService } from '../service.service';
import { FormBuilder } from '@angular/forms';
import { THIS_EXPR } from '@angular/compiler/src/output/output_ast';
declare var $ :any;

@Component({
  selector: 'app-catgories',
  templateUrl: './catgories.component.html',
  styleUrls: ['./catgories.component.css']
})
export class CatgoriesComponent implements OnInit {
  categoryList: any;
  addCategoryForm: any;
  responseData: any;
  image: string | ArrayBuffer;
  show:boolean=false;
  url: string | ArrayBuffer;
  submitted: boolean;
  textContent:any;
  categoryData: any = {};
  

  constructor(public route: Router, public service: ServiceService,public formBuilder:FormBuilder) { 
    this.addCategoryForm=formBuilder.group({
      categoryName:[''],
      file:[''],
      categoryId:[''],
      subcategoryId:['']
    })
  }

  ngOnInit() {
    this.route.navigate(['/catgories'])
    this.getCategoryList()
    
  }
  getCategoryList(): any {
    this.service.getApi('vendor/getCategoryList', 0).subscribe(Response => {
      if (Response['statusCode'] == 200) {
        console.log(Response['result'])
 this.categoryList= Response['result'];
      }
      else {
        console.log("error")
      }
    }, error => {
      console.log("error occur", error)
    })
  }


  changeListener($event) : void {
    console.log($event)
    this.readThis($event.target);
    this.readUrl($event.target);
  }
  
  readThis(inputValue: any): void {
    var file:File = inputValue.files[0];
    var myReader:FileReader = new FileReader();
  
    myReader.onloadend = (e) => {
      this.image = myReader.result;
      }
    myReader.readAsDataURL(file);
  }

  readUrl(inputValue:any) {
    if (inputValue.files && inputValue.files[0]) {
      var reader:FileReader = new FileReader();
  
      reader.onload = (event: ProgressEvent) => {
        this.url = reader.result;
      }
      console.log(this.url)
      reader.readAsDataURL(inputValue.files[0]);
    }
  }

  addCategory(val){
    let sentData = {
    categoryName: val.categoryName,
    image: this.image,
    icons: this.image,
   }
  this.service.postApi('vendor/addCategory', sentData, 0).subscribe(response => {

    this.responseData = response;
    console.log("WWWWWWWW", sentData)
    if (this.responseData['statusCode'] == 200) {
          this.route.navigate(['/categories']);
          this.ngOnInit();
    }
   }, error => {
    console.log('error occur', error)
    // this.service.showError('Server Error')
})
}
 clearAll()
 {
    if(this.categoryData) this.categoryData = {}
  this.url=''
  this.show =false
 }
deleteCategory(val){
  let sentData = {
    categoryId: val
   }
   this.service.postApi('admin/deleteCategory',sentData, 0).subscribe(response => {
    this.responseData = response;
    if (this.responseData['statusCode'] == 200) {
          this.route.navigate(['/categories']);
          this.ngOnInit();
       }
   }, error => {
    console.log('error occur', error)
    // this.service.showError('Server Error')
})
}

getCategory(val): any {
  this.service.getApi('admin/getCategoryId?categoryId='+val, 0).subscribe(Response => {
    if (Response['statusCode'] == 200) {
        this.categoryData = Response['result'];
        this.url = this.categoryData.icons
        this.show=true;
        console.log(this.categoryData)
    }
    else {
      console.log("error")
    }
  }, error => {
    console.log("error occur", error)
  })
}
 

editCategory(val){
  var get = $('#category_Id').val();
    let sentData = {
    categoryId: get,
    categoryName: val.categoryName
   }
    this.service.postApi('admin/editCategory',sentData, 0).subscribe(response => {
    this.responseData = response;
    if (this.responseData['statusCode'] == 200) {
          this.route.navigate(['/categories']);
          this.ngOnInit();
       }
   }, error => {
    console.log('error occur', error)
    // this.service.showError('Server Error')
})
}


onSubmit() {
  this.submitted = true;
  //console.log(this.hero);
}

}
