import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { ServiceService } from '../service.service';
import { FormBuilder } from '@angular/forms';
declare var $ :any;

@Component({
  selector: 'app-productcategories',
  templateUrl: './productcategories.component.html',
  styleUrls: ['./productcategories.component.css']
})
export class ProductcategoriesComponent implements OnInit {
  addProductCategoryForm: any;
  subcategoryList: any;
  productcategoryList: any;
  image: any;
  responseData: any;
  url: string | ArrayBuffer;
  submitted: boolean;
  show: boolean;
  productCategoriesData: any;

  constructor(public route: Router, public service: ServiceService,public formBuilder:FormBuilder) {
    this.addProductCategoryForm=formBuilder.group({
      productCategoryName:[''],
      subcategoryId:[''],
      file:[''],
      productCategoryId:['']
    })
   }
  ngOnInit() {
    this.route.navigate(['/productcategories'])
    this.getSubCategoryList()
    this.getProductCategoryList()
  }


getSubCategoryList(): any {
  this.service.getApi('vendor/getSubCategoryList?lang=en', 0).subscribe(Response => {
    if (Response['statusCode'] == 200) {
        this.subcategoryList= Response['result'];
        console.log(this.subcategoryList)
    }
    else {
       }
  }, error => {
     })
}


getProductCategoryList(): any {
  this.service.getApi('admin/getProductCategoryList?lang=en&status=ACTIVE', 0).subscribe(Response => {
    if (Response['statusCode'] == 200) {
        this.productcategoryList= Response['result'];
        console.log(this.productcategoryList)
    }
    else {
       }
  }, error => {
     })
}


changeListener($event) : void {
  this.readThis($event.target);
  this.readUrl($event.target);
}

readThis(inputValue: any): void {
  var file:File = inputValue.files[0];
  var myReader:FileReader = new FileReader();

  myReader.onloadend = (e) => {
    this.image = myReader.result;
    }
  myReader.readAsDataURL(file);
}

readUrl(inputValue:any) {
  if (inputValue.files && inputValue.files[0]) {
    var reader:FileReader = new FileReader();

    reader.onload = (event: ProgressEvent) => {
      this.url = reader.result;
    }
    reader.readAsDataURL(inputValue.files[0]);
  }
}


addProductCategory(val){
  let sentData = {
  subCategoryId:val.subcategoryId,
  productCategoryName: val.productCategoryName,
  image: this.image,
  status:'ACTIVE'
 }
 console.log(sentData);
this.service.postApi('vendor/addProductCategory', sentData, 0).subscribe(response => {
  console.log(response);
  this.responseData = response;
  if (this.responseData['statusCode'] == 200) {
        this.route.navigate(['/subcategories']);
        this.ngOnInit();
  }
 }, error => {
 })
}


deleteProductCategory(val){
  let sentData = {
    productCategoryId: val,
    status: 'INACTIVE'
   }
   console.log(sentData)
   this.service.postApi('admin/deleteProductCategory',sentData, 0).subscribe(response => {
    this.responseData = response;
    if (this.responseData['statusCode'] == 200) {
          this.route.navigate(['/productcategories']);
          this.ngOnInit();
       }
   }, error => {
    console.log('error occur', error)
    // this.service.showError('Server Error')
})
}

getProductCategory(val): any {
  console.log("dasdas")
  this.service.getApi('admin/getProductCategoryById?productCategoryId='+val, 0).subscribe(Response => {
    if (Response['statusCode'] == 200) {
      console.log(this.productCategoriesData)
        this.productCategoriesData = Response['result'];
        this.url = this.productCategoriesData.image
        this.show=true;
        console.log(this.productCategoriesData)
    }
    else {
      console.log("error")
    }
  }, error => {
    console.log("error occur", error)
  })
}


editProductCategory(val){
  var get = $('#productCategory_Id').val();
  let sentData = {
    productCategoryId: get,
    subCategoryId:val.subcategoryId,
    productCategoryName: val.productCategoryName,
    status:'ACTIVE'
   }
   this.service.postApi('admin/editProductCategory', sentData, 0).subscribe(response => {

    this.responseData = response;
   console.log("WWWWWWWW", sentData)
    if (this.responseData['statusCode'] == 200) {
          this.route.navigate(['/productcategories']);
          this.ngOnInit();
       }
   }, error => {
    console.log('error occur', error)
    // this.service.showError('Server Error')
})
}

clearAll()
{
if(this.productCategoriesData) this.productCategoriesData = {}
 this.url=''
 this.show =false
}

onSubmit() {
  this.submitted = true;
  //console.log(this.hero);
}
 
}