import { Component, OnInit } from '@angular/core';
import { ServiceService } from '../service.service';
import { ActivatedRoute, Router } from '@angular/router';

@Component({
  selector: 'app-productdetails',
  templateUrl: './productdetails.component.html',
  styleUrls: ['./productdetails.component.css']
})
export class ProductdetailsComponent implements OnInit {
  val: any;
  productDetails: any;
  
  constructor(public route: Router, public service: ServiceService,private activatedRoute: ActivatedRoute) {
    this.activatedRoute.queryParams.subscribe(params => {
      this.val = params['productId'];
      });
   }

  ngOnInit() {
    this.getProductDetails(this.val);
  }
  getProductDetails(val): any {
    this.service.getApi('vendor/productDetails?lang=en&_id='+val, 0).subscribe(Response => {
      if (Response['statusCode'] == 200) {
        this.productDetails= Response['result'];
        console.log(this.productDetails)
      }
      else {
        console.log("error")
      }
    }, error => {
      console.log("error occur", error)
    })
  }
  deleteReview(val,productId): any {
    let sentData ={
      reviewId: val,
      status: "inactive"
    }
    this.service.postApi('admin/changeReviewStatus?lang=en',sentData, 0).subscribe(Response => {
      if (Response['statusCode'] == 200) {
        this.productDetails= Response['result'];
        console.log(this.productDetails)
        this.route.navigate(['/productdetails?productId='+productId]);
          this.ngOnInit();
      }
      else {
        console.log("error")
      }
    }, error => {
      console.log("error occur", error)
    })
  }

}
