import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { LoginComponent } from './login/login.component';
import { DashboardComponent } from './dashboard/dashboard.component';
import { BrandsComponent } from './brands/brands.component';
import { VendorlistComponent } from './vendorlist/vendorlist.component';
import { CatgoriesComponent } from './catgories/catgories.component';
import { SubcatgoriesComponent } from './subcatgories/subcatgories.component';
import { CreatevendorComponent } from './createvendor/createvendor.component';
import { ForgotpasswordComponent } from './forgotpassword/forgotpassword.component';
import { ChatdetailsComponent } from './chatdetails/chatdetails.component';
import { UsersComponent } from './users/users.component';
import { UserdetailsComponent } from './userdetails/userdetails.component';
import { OrderlistComponent } from './orderlist/orderlist.component';
import { OrderdetailsComponent } from './orderdetails/orderdetails.component';
import { VendorproductsComponent } from './vendorproducts/vendorproducts.component';
import { ProductdetailsComponent } from './productdetails/productdetails.component';
import { ProductcategoriesComponent } from './productcategories/productcategories.component';




const routes: Routes = [
 {path:'',redirectTo: 'login',pathMatch:'full'},
 {path: 'login',component:LoginComponent},
 {path: 'dashboard',component:DashboardComponent},
 {path: 'brands',component:BrandsComponent},
 {path: 'vendorlist',component:VendorlistComponent},
 {path: 'catgories',component:CatgoriesComponent},
 {path: 'subcatgories',component:SubcatgoriesComponent},
 {path: 'createvendor',component:CreatevendorComponent},
 {path: 'forgotpassword',component:ForgotpasswordComponent},
 {path: 'chatdetails',component:ChatdetailsComponent},
 {path: 'users',component:UsersComponent},
 {path: 'userdetails',component:UserdetailsComponent},
 {path: 'orderlist',component:OrderlistComponent},
 {path: 'orderdetails',component:OrderdetailsComponent},
 {path: 'vendorproducts',component:VendorproductsComponent},
 {path: 'productdetails',component:ProductdetailsComponent},
 {path: 'productcategories',component:ProductcategoriesComponent}
 

];

@NgModule({
  imports: [RouterModule.forRoot(routes)],
  exports: [RouterModule]
})

export class AppRoutingModule {

 }
