import { Component, OnInit } from '@angular/core';
import { Router, ActivatedRoute } from '@angular/router';
import { ServiceService } from '../service.service';

@Component({
  selector: 'app-vendorproducts',
  templateUrl: './vendorproducts.component.html',
  styleUrls: ['./vendorproducts.component.css']
})
export class VendorproductsComponent implements OnInit {
  responseData: any;
  vendorDetails: any;
  productDetails: any;
  productCount: any;

  constructor(public route: Router, public service: ServiceService,private activatedRoute: ActivatedRoute) { }

  ngOnInit() {
    this.getProductsInfo("inactive");
    this.getProductsCount();
  }
  getProductsInfo(val){
      this.service.getApi('vendor/inActiveProductList?status='+val, 0).subscribe(Response => {
      if (Response['statusCode'] == 200) {
        this.vendorDetails= Response['result'];
        console.log(this.vendorDetails)
      }
      else if(Response['statusCode'] == 404) {
        this.vendorDetails= '';
      }
      else { 
        console.log("error")
      }
    }, error => {
      console.log("error occur", error)
    })
  }

  changeStatus(val,status): any {
    let sentData ={
      productId: val,
      status: status
    }
    this.service.postApi('admin/changeProductStatus?lang=en',sentData, 0).subscribe(Response => {
      if (Response['statusCode'] == 200) {
        this.productDetails= Response['result'];
        console.log(this.productDetails)
        this.route.navigate(['/vendorproducts']);
          this.ngOnInit();
      }
      else {
        console.log("error")
      }
    }, error => {
      console.log("error occur", error)
    })
  }


  getProductsCount(){
    this.service.getApi('admin/countProduct', 0).subscribe(Response => {
    if (Response['statusCode'] == 200) {
      this.productCount= Response['result'];
      console.log(this.productCount)
    }
    else { 
      console.log("error")
    }
  }, error => {
    console.log("error occur", error)
  })
}
}
