import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { ServiceService } from '../service.service';
// import * as $ from 'jquery';
import * as io from 'socket.io-client';
declare var $:any
@Component({
  selector: 'app-chatdetails',
  templateUrl: './chatdetails.component.html',
  styleUrls: ['./chatdetails.component.css']
})
export class ChatdetailsComponent implements OnInit {
  chatList: any;
  private socket: any; 
  public chatHistory : Array<any>;
  msg :any = "";
  receiverId : any = "";
  userName: any;
  i: any;

  constructor(public route: Router, public service: ServiceService) { 
    this.socket = io( service.baseUrl );
    this.socket.emit("initChat",{userId:localStorage.getItem("accessToken")})
    
    this.socket.on("sendmessage",(message)=>{
     // console.log("32333333",message)
      this.chatHistory.push(message.result);
    })
  }

  ngOnInit() {
    $('.chatboxmodel .titlerow .closesbtn button.close').click(function(){
      $('html').removeClass('chatbox');
      $('#sideMenu').css({'width':'0','height':'0','visibility':'hidden'});
  });
    this.getChatList()
    //this.sendMessage()
  }

  getChatList() {
    let sentData ={
     // userId:localStorage.getItem('accessToken')
      userId:"5bfbb0bbfd72a14b693fa9a0"
    }
      this.service.postApi('userConversationList', sentData,0).subscribe(Response => {
      if (Response['statusCode'] == 200) {
        this.chatList= Response['result'];
        
        console.log(this.chatList)
      }
      else {
        console.log("error")
      }
    }, error => {
      console.log("error occur", error)
    })
  }

  openChat(receiverId:string){
    this.receiverId = receiverId;
    let sentData = {
      "senderId":localStorage.getItem('accessToken'),
	    "receiverId":this.receiverId,
	    "pagenumber":"1"
    }
   
    this.service.postApi('ChatHistory', sentData,0).subscribe(Response => {
      if (Response['statusCode'] == 200) {
        this.chatHistory= Response['chatResult'];
       console.log(this.chatHistory)
       for (let i = 0; i < this.chatList.length; i++) {
        // const element = array[index];
        // debugger
        if(this.chatHistory[0].senderId == this.chatList[i].participant_id)
        {
          this.userName=this.chatList[i].userName
          break
        }
        else{
          this.userName="asdfasffasf"
        }
       }
       
      }
      else {
        console.log("error")
      }
    }, error => {
      console.log("error occur", error)
    })

      $('.chatboxmodel').show();
      $('.chatboxmodel').css({'width':'300px','height':'400px','visibility':'visible'});
      if($('.chatboxmodel').css('display')=='block'){
             $('html').addClass('chatbox');
      }
      // $('.livechaticon').click(function(){
        $(".chatsection").scrollTop(99999999999999999999999);
        // });
  }

  sendMessage(){
    console.log("SEND")
    this.socket.emit("sendmessage",{senderId:localStorage.getItem("accessToken"),receiverId:this.receiverId,message:this.msg})
    this.msg = ""
   // this.openChat(this.receiverId);
  }
}
