import { Component, OnInit } from '@angular/core';
import { Router, ActivatedRoute } from '@angular/router';
import { ServiceService } from '../service.service';

@Component({
  selector: 'app-orderlist',
  templateUrl: './orderlist.component.html',
  styleUrls: ['./orderlist.component.css']
})
export class OrderlistComponent implements OnInit {
  orderList: any;
  val: any;

  constructor(public route: Router, public service: ServiceService,private activatedRoute: ActivatedRoute) { 
    this.activatedRoute.queryParams.subscribe(params => {
      this.val = params['userId'];
      });
    }
  ngOnInit() {
    this.getOrderList(this.val);
  }
  getOrderList(val): any {
    this.service.getApi('vendor/orderList?lang=en&userId='+val, 0).subscribe(Response => {
      if (Response['statusCode'] == 200) {
        this.orderList= Response['result'];
        console.log(this.orderList)
      }
      else {
        console.log("error")
      }
    }, error => {
      console.log("error occur", error)
    })
  }

}



