import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { ServiceService } from '../service.service';
import { FormControl, FormGroup, Validators,ReactiveFormsModule, FormBuilder } from '@angular/forms';
declare var $ :any;

@Component({
  selector: 'app-brands',
  templateUrl: './brands.component.html',
  styleUrls: ['./brands.component.css']
})


export class BrandsComponent implements OnInit {
  brandList: any;
  addBrandForm:any;
  sentData : any;
  responseData: any;
  image: string | ArrayBuffer;
  url: string | ArrayBuffer;
  submitted: boolean;
  show:boolean=false;
  brandData: any;
  constructor(public route: Router, public service: ServiceService,public formBuilder:FormBuilder) { 
  this.addBrandForm=formBuilder.group({
    brandName:[''],
    brandId:[''],
    file:['']
  })
  }

  ngOnInit() {
    this.route.navigate(['/brands'])
    this.getBrandList()
    
  }
  getBrandList() {
    this.service.getApi('admin/getBrandList', 0).subscribe(Response => {
      if (Response['statusCode'] == 200) {
        console.log(Response['result'])
 this.brandList= Response['result'];
      }
      else {
        console.log("error")
      }
    }, error => {
      console.log("error occur", error)
    })
  }

  changeListener($event) : void {
    console.log($event)
    this.readThis($event.target);
    this.readUrl($event.target);
  }
  
  readThis(inputValue: any): void {
    var file:File = inputValue.files[0];
    var myReader:FileReader = new FileReader();
  
    myReader.onloadend = (e) => {
      this.image = myReader.result;
      }
    myReader.readAsDataURL(file);
  }

  readUrl(inputValue:any) {
    if (inputValue.files && inputValue.files[0]) {
      var reader:FileReader = new FileReader();
  
      reader.onload = (event: ProgressEvent) => {
        this.url = reader.result;
      }
      console.log(this.url)
      reader.readAsDataURL(inputValue.files[0]);
    }
  }
  addBrand(val){
    this.sentData = {
    brandName: val.brandName,
    brandImage: this.image,
   }
  this.service.postApi('admin/addBrand', this.sentData, 0).subscribe(response => {

    this.responseData = response;
   // console.log("WWWWWWWW", this.responseData['statusCode'])
    if (this.responseData['statusCode'] == 200) {
          this.route.navigate(['/brands']);
          this.ngOnInit();
    }
   }, error => {
    console.log('error occur', error)
    // this.service.showError('Server Error')
})
 }
clearAll()
{
if(this.brandData) this.brandData = {}
 this.url=''
 this.show =false
}

getBrand(val): any {
  this.service.getApi('admin/getBrandById?brandId='+val, 0).subscribe(Response => {
    if (Response['statusCode'] == 200) {
      console.log(this.brandData)
        this.brandData = Response['result'];
        this.url = this.brandData.icon
        this.show=true;
        console.log(this.brandData)
    }
    else {
      console.log("error")
    }
  }, error => {
    console.log("error occur", error)
  })
}

editBrand(val){
  var get = $('#brand_Id').val();
  this.sentData = {
    brandId: get,
    brandName: val.brandName
   }
   this.service.postApi('vendor/updateBrand', this.sentData, 0).subscribe(response => {

    this.responseData = response;
   console.log("WWWWWWWW", this.sentData)
    if (this.responseData['statusCode'] == 200) {
          this.route.navigate(['/brands']);
          this.ngOnInit();
       }
   }, error => {
    console.log('error occur', error)
    // this.service.showError('Server Error')
})
}

deleteBrand(val){
  this.sentData = {
    brandId: val
   }
   this.service.postApi('vendor/deleteBrand', this.sentData, 0).subscribe(response => {
    this.responseData = response;
    if (this.responseData['statusCode'] == 200) {
          this.route.navigate(['/brands']);
          this.ngOnInit();
       }
   }, error => {
    console.log('error occur', error)
    // this.service.showError('Server Error')
})
}
onSubmit() {
  this.submitted = true;
  //console.log(this.hero);
}
 
}
